
import { Options, Vue } from 'vue-class-component';
import { ComponentData, variables, progressbars, images, logs, lineCharts } from "@/data";
import ProgressBarTable from "@/components/progressbar/ProgressBarTable.vue";
import VariableTable from "@/components/variable/VariableTable.vue";
import ImageSection from "@/components/image/ImageSection.vue";
import LoggerSection from "@/components/logger/LoggerSection.vue";
import PlotSection from "@/components/plot/PlotSection.vue";

type RequestCache = "default" | "force-cache" | "no-cache" | "no-store" | "only-if-cached" | "reload";


@Options({
  components: {
    ProgressBarTable,
    VariableTable,
    ImageSection,
    LoggerSection,
    PlotSection
  },
})
export default class App extends Vue {
  readonly variables = variables
  readonly progressbars = progressbars
  readonly images = images
  readonly logs = logs
  readonly lineCharts = lineCharts
  updateDuration = 5000
  view: string = null

  $refs!: {
    progressbar: ProgressBarTable,
    variable: VariableTable,
    image: ImageSection,
    logger: LoggerSection,
    plot: PlotSection
  }

  update(cache: RequestCache = "no-cache"): void {
    fetch("/data.json", {cache: cache})
        .then(response => response.json())
        .then(json => {
          const changes: Array<{type: string, name: string}> = [];

          function updateArray(d: ComponentData, array: {[name: string]: ComponentData}) {
            const prev = array[d.name];
            if (prev === undefined) {
              array[d.name] = d;
            } else if (prev.value !== d.value) {
              array[d.name] = d;
              changes.push({type: d.type, name: d.name});
            }
          }

          this.view = json.view;

          json.data.forEach((d: ComponentData) => {
            if (d.type === "Variable") {
              updateArray(d, this.variables);
            } else if (d.type === "ProgressBar") {
              updateArray(d, this.progressbars);
            } else if (d.type === "Image") {
              updateArray(d, this.images);
            } else if (d.type === "Logger") {
              updateArray(d, this.logs)
            } else if (d.type === "LineChart") {
              updateArray(d, this.lineCharts)
            }
          });

          changes.forEach(({type, name}) => {
            if (type === "ProgressBar") {
              this.$refs.progressbar.$refs[name].update();
            } else if (type === "Variable") {
              this.$refs.variable.$refs[name].update();
            } else if (type === "Image") {
              this.$refs.image.$refs[name].update();
            } else if (type === "Logger") {
              this.$refs.logger.$refs[name].update();
            } else if (type === "LineChart") {
              this.$refs.plot.$refs[name].update();
            }
          });

          setTimeout(this.update, Math.max(100, this.updateDuration));
        })
        .catch(() => {
          setTimeout(() => this.update("reload"), Math.max(100, this.updateDuration))
        });
  }

  mounted(): void {
    this.update();
  }

  get nPlots(): number {
    return Object.keys(this.lineCharts).length;
  }
}
